.wrapper {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: white;
    margin-top: -20px;
    font-size: 18px;
  }

  .contactWrapper{
    display: flex;
  }

  .contactWrapper a{
    font-weight: bold;
    text-decoration: none;
    color: #1d44b8;
  }

  .icon{
    margin-top: -2px;
    margin-right: 5px;
  }

  .socialIcon{
    padding: 20px;
  }

  .socialComponent{
    margin-top: 20px;
  }

  @media screen and (max-width:400px) {
    .socialIcon{
      padding: 8px;
    }
  }

  @media screen and (max-width:290px) {
    .socialIcon{
      padding: 5px;
    }
  }